.section-brands {
  .brands-list-wrapper {
    .brands-letters {
      font-size: 1rem;
      position: sticky;
      top: clamp(0px, calc(var(--header-height, 0px) + 1px + var(--header-offset, 0px)), calc(var(--header-height, 0px) + 1px));
      padding-top: 13px;
      padding-bottom: 13px;
      margin: 0;
      z-index: 40;
      border-bottom: 1px solid var(--color-header-border, var(--color-border));
      transition: all 0.2s ease;
      @media only screen and (min-width: 768px) {
        border-bottom: none;
      }
      &.is-sticky {
        background-color: var(--color-header-bg, #fff);
        border-bottom: 1px solid var(--color-header-border, var(--color-border));
        &::after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          bottom: 0;
          z-index: -1;
          pointer-events: none;
          box-shadow: 0 9px 38px rgba(0, 0, 0, 0.05), 0 5px 12px rgba(0, 0, 0, 0.1);
        }
      }
      .carousel__slide {
        > a {
          display: flex;
          align-items: center;
          justify-content: center;
          min-width: 25px;
        }
      }
      .slider-item {
        &:not(:first-child) {
          margin-left: 19px;
        }
        a {
          &.is-selected {
            font-weight: 700;
          }
          color: var(--color-body, #000000);
          &:not([href]) {
            cursor: default;
            pointer-events: none;
            opacity: 0.3;
          }
        }
      }
    }
  }
  .carousel:not(.flickity-enabled) {
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: -ms-autohiding-scrollbar;
    cursor: grab;
    display: flex;
    gap: 20px;
    flex-wrap: nowrap;
    list-style-type: none;
    max-width: 100%;
    overflow-x: scroll;
    overflow-y: hidden;
    scroll-behavior: auto;
    scroll-snap-type: x mandatory;
    scrollbar-width: none;
    justify-content: space-between;
    &::-webkit-scrollbar {
      display: none;
      height: 0;
      width: 0;
    }
    .slider-item {
      flex: 0 0 auto;
      user-select: none;
    }
  }
  .brands-list {
    padding-top: 40px;
    padding-bottom: 40px;
    > .brands-item {
      margin: 0;
      > * {
        margin-bottom: 0;
      }
    }
    > * {
      margin-bottom: 0;
      padding-top: 12px;
      padding-bottom: 12px;
      &:hover {
        background-color: var(--color-grey-bg, #efefef);
      }
    }
  }
  .brands-list__image-wrapper {
    display: none;
    background-color: var(--color-grey-bg, #efefef);
    @media only screen and (min-width: 768px) {
      display: block;
      position: sticky;
      top: var(--header-height, 0px);
      height: calc(100vh - var(--header-height, 0px));
      max-width: 50vw;
      aspect-ratio: 3 / 4;
      .brands-list__image {
        pointer-events: none;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}
