.z-0 {
  z-index: 0!important;
}

.-z-1 {
  z-index: -1!important;
}

.transition-none {
  transition: none!important;
}

.flex-1 {
  flex: 1;
}

// gowish button added through script in theme.liquid
// !important is used to override the default styles

.onske_knap {
  display: block;
  .gowish-btn {
    width: 100%;
    border-radius: 0!important;
    border: .5px solid var(--outline-button-label, var(--color-accent))!important;
    color: var(--outline-button-label, var(--color-accent))!important;
    background: transparent!important;
    font-family: inherit!important;
    text-transform: unset!important;
    background-image: url(https://inspiration.onskeskyen.dk/wp-content/uploads/2022/11/gowish-final-cloud_2_sort.svg) !important;
    background-position: calc(40% - 70px) !important;
    background-repeat: no-repeat !important;
    background-size: auto 18px !important;
    padding: 0 20px 0 30px !important;
    margin-bottom: 10px !important;
    text-align: center;
    margin-right: 0 !important;
    height: 45px !important;
  }
}

.onske_knap:has(+ .shopify-app-block wishlist-button-block) {
  width: 63%;
  margin-left: 2%;
  float: right;

  &+.shopify-app-block {
    width: 35%;
    float: left;
    font-size: 12px;

    button {
      width: 100%;
    }
  } 
}
@include create-mq("tablet-and-up") {
  .onske_knap:has(+ .shopify-app-block wishlist-button-block) {
    width: 73%;
    margin-left: 2%;

    +.shopify-app-block {
      width: 25%;
    } 
  }
}