.product-inventory-notice--wrapper {
  margin-bottom: 16px;
}

.store-availability {
  color: var(--color-body);
  font-size: 15px;
  .open-side-panel {
    text-decoration: underline;
    cursor: pointer;
    opacity: .8;
    &:hover {
      opacity: 1;
      transition: all .1s ease-in-out;
    }
  }
}

.side-panel-wrapper {
  .side-panel {
    width: 100%;
    max-width: unset;

    .side-panel__header  {
      .title {
        font-weight: 500;
        text-transform: uppercase;
        position: relative;
        font-size: 17px;
      }
      .close-side-panel {
        width: 20px;
        cursor: pointer;
        .bar {
          width: 1px;
          height: 15px;
          background-color: var(--color-body);
          top: calc(50% - 10px);
          left: calc(50% - 1px);
          &.left {
            transform: rotate(45deg);
          }
          &.right {
            transform: rotate(-45deg);
          }
        }
      }
    }

    .side-panel__description {
      font-size: 15px;
      opacity: .8;
    }

    .side-panel__enable-location {
      cursor: pointer;
      text-decoration: underline;
      font-size: 15px;
    }

    .pickup-locations {
      .pickup-location {
        border-bottom: 1px solid rgb(var(--color-body-rgb), .2);
      }
      .pickup-location:last-of-type {
        border-bottom: none;
      }
      .pickup-location__title {
        line-height: 1.3;
        font-size: 17px;

        .product-inventory-notice {
          margin-left: -24px;
        }
      }
      .pickup-location__distance {
        opacity: .8;
      }
      .pickup-location__body {
        font-size: 16px;
      }
      .pickup-location__items {
        font-size: 15px;
        opacity: .8;
      }
    }
  }
  
  @include create-mq("tablet-and-up") {
    .side-panel {
      max-width: 500px;

      &.active + .side-panel-background {
        width: 100%;
        left: 0;
        transform: unset;
        z-index: 51;
        background: var(--color-body);
        opacity: .3;
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        overflow-x: hidden;
      }
    }
  }
}