.gift-wrap-checkbox-section {
    margin-top: 8px;
    position: relative;
    display: flex;
    gap: 5px;
    font-size: 0.6875rem;
    input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;
    }

    .checkmark {
        position: relative;
        height: 0.6875rem;
        width: 0.6875rem;
        margin: auto 0;
        border: 1px solid var(--color-border);
    }

    & input:checked ~ .checkmark:after {
        display: block;
        position: absolute;
        top: 0;
        left: 0.2rem;
        width: 4px;
        height: 8px;
        border: solid black;
        border-width: 0 1px 1px 0;
        -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);
    }

    & .checkmark:after {
        content: "";
        display: none;
    }
}

.gift-wrap-price {
    font-size: 12px;
    padding-top: 10px;
    white-space: nowrap;
}