.product-variants-dropdown {
  form {
    $height: 30px;
    height: $height;
    select {
      cursor: pointer;
      border: none;
      font-size: 14px;
      background-image:
        linear-gradient(45deg, transparent 50%, var(--color-body) 50%),
        linear-gradient(135deg, var(--color-body) 50%, transparent 50%);
      background-position:
        calc(100% - 8px) 12px,
        calc(100% - 3px) 12px;
      background-size:
        5px 5px,
        5px 5px;
      background-repeat: no-repeat;
      height: $height;
      padding-left: 5px;
      margin-left: -5px;
    }
    
    button[type="submit"] {
      height: $height;
      width: $height;
      position: relative;
      border: 1px solid var(--color-body);
      margin-top: -10px;

      &::before, &::after {
        position: absolute;
        background: var(--color-body);
        content: "";
        display: block;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      &::before {
        width: 1px;
        height: 17px;
      }
      &::after {
        width: 17px;
        height: 1px;
      }

      &.selected {
        background: var(--color-body);

        &::before, &::after {
          background: var(--bg-body);
          transform-origin: left bottom;
        }
        &::before {
          width: 2px;
          height: 7px;
          transform: translate(calc(50% - 4px), calc(50% - 3px) )rotate(-45deg);
        }
        &::after {
          height: 2px;
          transform: translate(calc(50% - 11px), calc(50% + 4px))rotate(-45deg);
        }
      }
    }
  }
}

product-recommendations {
  .product-cart-item {
    max-width: unset;
  }
}
