.side-panel, .cart-items {
    .side-panel-scale-point, .side-panel-gift-wrap, .main-cart-scale-point {

        input[type=checkbox]:checked~.scale-point-info, 
        input[type=checkbox]:checked~.scale-point-info.main-cart {
            display: block !important;
        }

        .scale-point-info {
            position: relative;
            margin-left: 5px;
            .scale-point-info-text {
                background-color: var(--bg-body);
                position: absolute;
                top: 100%;
                transform: translateX(-20%);
                -webkit-transform: translateX(-20%);
                visibility: hidden;
                border: 1px solid rgba(245,245,244,0.8);
                padding: 5px;
                width: auto;
                height: auto;
                z-index: 99;
            }
        }

        .scale-point-info:hover .scale-point-info-text {
            visibility: visible;
        }
    }

    .side-panel-scale-point .scale-point-checker,
    .main-cart-scale-point .scale-point-checker.main-cart,
    .side-panel-personal-card .personal-card-checker,
    .main-cart-personal-card .personal-card-checker,
    .side-panel-gift-wrap .gift-wrap-checker {
        align-items: center;
        flex-wrap: wrap;
        font-size: 12px;
        padding-top: 10px;
        width: 100%;

        .scale-point-text-area.first-input-mt,
        .personal-card-text-area.first-input-mt,
        .personal-card-text-area.first-input-mt.main-cart {
            margin-top: 12px;
        }

        input[type=checkbox] {
            position: absolute;
            visibility: hidden;
        }

        .check-wrapper, .check-wrapper.main-cart {
            height: 15px;
            width: 15px;
            position: relative;
            margin-right: 5px;
            .checked {
                opacity: 0;
            }
            svg {
                position: absolute;
                top: 0;
                left: 0;
            }
        }

        .scale-point-text-area,
        .scale-point-text-area.main-cart,
        .personal-card-text-area,
        .personal-card-text-area.main-cart {
            width: 100%;
            font-size: 12px;
            height: 45px;
            padding: 12px 15px; 
            margin-top: 4px;
            margin-bottom: 0;
            border: 0.5px solid var(--color-input-text);
        }

        input[type=checkbox]:checked~.scale-point-text-area,
        input[type=checkbox]:checked~.scale-point-text-area.main-cart,  
        input[type=checkbox]:checked~.personal-card-text-area,
        input[type=checkbox]:checked~.personal-card-text-area.main-cart {
            display: block !important;
        }

        input[type=checkbox]:checked+.check-wrapper .checked,
        input[type=checkbox]:checked+.check-wrapper.main-cart .checked,
        input[type=checkbox]:checked+.check-wrapper.main-cart .checked {
            opacity: 1;
        }

        input[type=checkbox]:checked+.check-wrapper .empty,
        input[type=checkbox]:checked+.check-wrapper.main-cart .empty,
        input[type=checkbox]:checked+.check-wrapper.main-cart .empty {
            opacity: 0;
        }
    }
    
    .side-panel-personal-card,
    .main-cart-personal-card {
        .personal-card-for-free,
        .personal-card-for-free.main-cart {
            margin-left: auto;
            font-weight: 700;
        }

        .personal-card-checker {
            textarea.personal-card-text-area,
            textarea.personal-card-text-area.main-cart {
                resize: none;
                min-height: unset;
                height: 100px;
                margin-top: 4px;
                margin-bottom: 0;
                border: 0.5px solid var(--color-input-text);
            }
        }
    }
}