// Create media query with breakpoints
@mixin media-breakpoint-up($breakpoint) {
  @if ($breakpoint == 0) {
    @content;
  } @else {
    @media (min-width: $breakpoint) {
      @content;
    }
  }
}

@mixin media-breakpoint-down($breakpoint) {
  @if ($breakpoint == 0) {
    @content;
  } @else {
    @media (max-width: $breakpoint) {
      @content;
    }
  }
}

// Create media query with alias
@mixin create-mq($alias) {
  @if ($alias) {
    @each $ssName, $ssValue in $screenSizes {
      @if ($ssName == $alias) {
        @media #{$ssValue} {
          @content;
        }
      }
    }
  }
}

// Create column classes
@mixin create-col-classes($modifier, $grid-columns, $breakpoint) {
  @include media-breakpoint-up($breakpoint) {
    @for $i from 1 through $grid-columns {
      @if ($breakpoint == 0) {
        .col-#{$i} {
          flex-basis: math.div(100, math.div($grid-columns, $i)) * 1%;
          max-width: math.div(100, math.div($grid-columns, $i)) * 1%;
          padding: 0 10px;
        }
      } @else {
        .col-#{$modifier}-#{$i} {
          flex-basis: math.div(100, math.div($grid-columns, $i)) * 1%;
          max-width: math.div(100, math.div($grid-columns, $i)) * 1%;
          padding: 0 10px;
        }
      }
    }
    @if ($breakpoint == 0) {
      .col-2-4 {
        flex-basis: 20%;
        max-width: 20%;
        padding: 0 10px;
      }
    } @else {
      .col-#{$modifier}-2-4 {
        flex-basis: 20%;
        max-width: 20%;
        padding: 0 10px;
      }
    }
    @if ($breakpoint == 0) {
      .col-auto {
        flex-basis: auto;
        max-width: auto;
        padding: 0 10px;
      }
    } @else {
      .col-#{$modifier}-auto {
        flex-basis: auto;
        max-width: auto;
        padding: 0 10px;
      }
    }
  }
}
