// Display utilities
@each $ssName, $ssValue in $grid-breakpoints {
  @if $ssValue == 0 {
    @each $disp in $displayProps {
      .d-#{$disp} {
        display: #{$disp} !important;
      }
    }
  } @else {
    @media (min-width: $ssValue) {
      @each $disp in $displayProps {
        .d-#{$ssName}-#{$disp} {
          display: #{$disp} !important;
        }
      }
    }
  }
}

// Visibility utilities
@each $ssName, $ssValue in $grid-breakpoints {
  @if $ssValue == 0 {
    @each $visibility in $visibilities {
      .v-#{$visibility} {
        visibility: #{$visibility};
      }
    }
  } @else {
    @media (min-width: $ssValue) {
      @each $visibility in $visibilities {
        .v-#{$ssName}-#{$visibility} {
          visibility: #{$visibility};
        }
      }
    }
  }
}

// Text alignment utilities
@each $ssName, $ssValue in $grid-breakpoints {
  @if $ssValue == 0 {
    @each $textAlign in $textAlignments {
      .text-#{$textAlign} {
        text-align: #{$textAlign};
      }
    }
  } @else {
    @media (min-width: $ssValue) {
      @each $textAlign in $textAlignments {
        .text-#{$ssName}-#{$textAlign} {
          text-align: #{$textAlign};
        }
      }
    }
  }
}

// Overflow utilities
@each $overflow in $overflows {
  .overflow-#{$overflow} {
    overflow: #{$overflow};
  }
}

// Vertical align
@each $verticalAlign in $verticalAligns {
  .align-#{$verticalAlign} {
    vertical-align: #{$verticalAlign};
  }
}

// Position
@each $position in $positions {
  .position-#{$position} {
    position: #{$position};
  }
}

// Grid layout(Flexbox)
.row {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0 -1rem;
}

.col {
  flex: 1 0 0;
  padding: 0 1rem;
}

@each $modifier, $breakpoint in $grid-breakpoints {
  @include create-col-classes($modifier, $grid-columns, $breakpoint);
}

// Align items
@each $alignItem in $alignItems {
  .align-items-#{$alignItem} {
    align-items: #{$alignItem};
  }
}

// Justify content
@each $justifyContent in $justifyContents {
  .justify-content-#{$justifyContent} {
    justify-content: #{$justifyContent};
  }
}

// Flex wrap
@each $fw in $flexWrap {
  .flex-wrap-#{$fw} {
    flex-wrap: #{$fw};
  }
}

// Spacing utilities
@each $ssName, $ssValue in $grid-breakpoints {
  @if $ssValue == 0 {
    @each $spacingName, $spacingValue in $spacingUtilities {
      @each $sidesName, $sidesValue in $spacingSides {
        @each $unit in $spacingUnits {
          @if $sidesName == "x" {
            .#{$spacingName}#{$sidesName}-#{$unit} {
              #{$spacingValue}-left: $unit * 10px !important;
              #{$spacingValue}-right: $unit * 10px !important;
            }
          } @else if $sidesName == "y" {
            .#{$spacingName}#{$sidesName}-#{$unit} {
              #{$spacingValue}-top: $unit * 10px !important;
              #{$spacingValue}-bottom: $unit * 10px !important;
            }
          } @else {
            .#{$spacingName}#{$sidesName}-#{$unit} {
              #{$spacingValue}-#{$sidesValue}: $unit * 10px !important;
            }
          }
        }
      }
    }
  } @else {
    @media (min-width: $ssValue) {
      @each $spacingName, $spacingValue in $spacingUtilities {
        @each $sidesName, $sidesValue in $spacingSides {
          @each $unit in $spacingUnits {
            @if $sidesName == "x" {
              .#{$spacingName}#{$sidesName}-#{$ssName}-#{$unit} {
                #{$spacingValue}-left: $unit * 10px !important;
                #{$spacingValue}-right: $unit * 10px !important;
              }
            } @else if $sidesName == "y" {
              .#{$spacingName}#{$sidesName}-#{$ssName}-#{$unit} {
                #{$spacingValue}-top: $unit * 10px !important;
                #{$spacingValue}-bottom: $unit * 10px !important;
              }
            } @else {
              .#{$spacingName}#{$sidesName}-#{$ssName}-#{$unit} {
                #{$spacingValue}-#{$sidesValue}: $unit * 10px !important;
              }
            }
          }
        }
      }
    }
  }
}