#clerk-show-facets {
    width: 70%;
    height: 40px;
    margin: 20px auto;
    background-color: #333;
    color: white;
    text-align: center;
    border-radius: 2px;
    line-height: 40px;
    cursor: pointer;
}

.side-panel.search-drawer .side-panel-inner {
    height: reset;
}

.clerk-page-width {
    display: flow-root;
}

#clerk-search-results {
    width: 100%;
    padding-bottom: 2rem;
}

.clerk-search-result-title {
    font-size: 14px;
    color: inherit;
    margin-bottom: 0;
    font-family: "Twentieth Century", sans-serif;
}

.clerk_display_toggle {
    flex-direction: row-reverse;
    font-weight: 600;
    font-size: .75rem;
    text-transform: uppercase;
    margin-right: 30px;
    letter-spacing: .1em;
    color: var(--color-body);
}

#clerk-show-facets {
    display: none;
}

.clerk_flex_wrap {
    display: flex;
    flex-direction: row;
    width: 100%;
    padding: 0 50px;
}

.clerk .active {
    display: block;
}

@media only screen and (max-width : 800px) {
    #clerk-search-filters {
        width: 100% !important;
    }


    #clerk-show-facets {
        display: block;
    }

    .clerk_flex_wrap {
        flex-direction: column;
    }

    #clerk-search-results {
        display: block;
        width: 100%;
    }
}

@media only screen and (max-width : 767px) {
    .clerk_flex_wrap {
        padding: 11px;
    }
}