.products {
  &.products-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: 1fr;
    margin-left: -0.9375rem;
    margin-right: -0.9375rem;
    .promotion-column {
      grid-column: span 2;
    }
    @media (min-width: 48em) {
      &.cols-md-3 {
        grid-template-columns: repeat(3, 1fr);
      }
      &.cols-md-4 {
        grid-template-columns: repeat(4, 1fr);
      }
      &.cols-md-5 {
        grid-template-columns: repeat(5, 1fr);
      }

      .promotion-column-width--1 {
        grid-column: span 1;
      }
      .promotion-column-width--2 {
        grid-column: span 2;
      }
      .promotion-column-width--4 {
        grid-column: span 2;
        grid-row: span 2;
      }
    }
  }
}
